  <div class="content">
    <h1>罕见！80岁的市委原书记被缺席审判 20年前携巨款和情妇外逃</h1>
    <p>今天的话题，是程三昌。</p>
    <p>12月9日上午，最高检举行“积极适用违法所得没收程序，促进反腐败国际追逃追赃”新闻发布会。</p>
    <p>
      发布会提到了外逃贪官程三昌，称“
      <strong>河南省郑州市人民检察院首次适用缺席审判程序对潜逃境外的贪污犯罪嫌疑人程三昌提起公诉</strong>”。
    </p>
    <p>程三昌是“百名红通人员”，担任过河南省漯河市委原书记、豫港（集团）有限公司原董事长。</p>
    <p>
      当日，郑州市中院公开开庭缺席审理了程三昌贪污一案。该案是
      <strong>我国适用刑事缺席审判程序审理的第一起外逃被告人贪污案。</strong>
    </p>
    <p>此次审理，意义重大。</p>
    <p>
      <strong>2001年外逃，曾被称为“程卖光”</strong>
    </p>
    <p>先来说一下，程三昌是谁。</p>
    <p>公开资料显示，程三昌于1996年到1999年担任河南省漯河市市委书记，他原是王有杰（已获刑）任漯河市委书记时的市长。1995年，王有杰升迁后，程三昌接任了漯河市委书记一职。</p>
    <p>
      <img class="content-picture" src="~images/homeImg/newsImg/3-1.jpg" />
    </p>
    <p>此后，程三昌又担任河南省政府驻香港豫港集团有限公司董事长。</p>
    <p>程三昌曾被认为是一位“敢说敢干”的干部，以“勇敢闯禁区，大胆卖企业”而闻名。但之后调查发现，这些交易均有大量猫腻，国有资产严重流失。</p>
    <p>据媒体报道，程三昌主政漯河市期间，曾亲自经手卖掉国有企业100多家，占了全市90%以上的国有企业，获名“程卖光”。</p>
    <p>有媒体记者跟踪调查了经程三昌之手卖掉的漯河市制药厂、市第二针织厂、威士达皮鞋厂三家国企，发现程三昌撇开公开竞价的政策，与不法商人暗中勾结，致使国有资产流失，职工利益受到侵害。</p>
    <p>程三昌隐藏得很深。在他主政漯河期间“大刀阔斧”倡导改革之时，他已经在为自己的“未来”做准备了。</p>
    <p>2001年2月，由于发现风声不对，他从香港不辞而别，携巨款和情妇定居新西兰，出逃时他61岁。</p>
    <p>2002年2月8日，国际刑警组织对其发布红色通报。</p>
    <p>据披露，程三昌早前就利用关系在境外开设办事处或分公司，并暗地转移资产，自己长期游移于境内外之间，一旦东窗事发，便不再回国。</p>
    <p>
      据《方圆法治》报道，
      <strong>程三昌跑到新西兰后成了当地大款，在奥克兰市瑞克大街购有一栋四层豪华别墅，过上了豪华舒适的生活，而漯河却要为程三昌外逃买单。</strong>
    </p>
    <p>在国有资产流失的同时，处于社会底层的工人、农民的利益很难得到保障，程三昌外逃后漯河出现了大规模的上访。</p>
    <p>
      2003年，《半月谈》曾发文提到，程三昌在新西兰的奥克兰购有豪华别墅和汽车，
      <strong>河南省有关部门曾专门派干部到新西兰找他，劝其回国，乐不思蜀的程未予理会。他说，“最好不要回去”“钱存在我的个人户头里，你们拿我也没办法”。</strong>
    </p>
    <p>另外，程三昌潜逃新西兰后，还开始“著书立说”，披露当年在出售国有资产中，如何与不法奸商暗中勾结、中饱私囊，致使国有资产严重流失的种种内幕。</p>
    <p>
      <strong>未到案</strong>
    </p>
    <p>12月9日，河南省郑州市中院公开开庭缺席审理了程三昌贪污一案。该案开庭时，程三昌80岁。</p>
    <p>据郑州市人民检察院指控：</p>
    <p>2000年12月7日至2000年12月15日，被告人程三昌利用担任豫港（集团）有限公司董事长的职务便利，以在新西兰设立分公司为由，先后三次指使财务人员将公款转入其名下支票账户及其在新西兰开设的个人账户，非法占有公款港元、新西兰元、美元折合人民币共计308.88万余元。</p>
    <p>郑州市人民检察院提请适用缺席审判程序，以贪污罪追究其刑事责任。</p>
    <p>
      郑州市中级人民法院依法
      <strong>将传票和起诉书副本送达被告人程三昌后，程三昌未按要求到案。</strong>
    </p>
    <p>根据法院发布的画面，在“被告人”席位上，贴着“程三昌”的名字。</p>
    <p>
      <img class="content-picture" src="~images/homeImg/newsImg/3-2.jpg" />
      <i class="desc">程三昌的近亲属代为委托的辩护人到庭参加诉讼。</i>
    </p>
    <p>
      庭审中，控辩双方分别出示了相关证据，并进行了质证，在法庭主持下充分发表了意见，
      <strong>程三昌的近亲属委托辩护人代其宣读了最后意见。</strong>
    </p>
    <p>该案择期宣判。</p>
    <p>如果之后他被抓了回来，怎么办？</p>
    <p>
      值得一提的是，这是
      <strong>我国适用刑事缺席审判程序审理的第一起外逃被告人贪污案。</strong>
    </p>
    <p>
      据清华大学法学院教授张建伟介绍，
      <strong>缺席审判，对应的是“对席审判”，是指法院在被告人不出庭的情况下，对案件进行审理和判决。</strong>
    </p>
    <p>刑事诉讼原则上实行对席审判，只有特殊情况下实行缺席审判。这是因为刑事审判涉及个人或者单位的罪责问题，干系重大。</p>
    <p>
      2018年10月，十三届全国人大常委会第六次会议通过了修改刑事诉讼法的决定，修改后的刑事诉讼法第五编特别程序中增设“缺席审判程序”一章，
      <strong>在我国刑事法律中第一次建立了刑事缺席审判制度。</strong>
    </p>
    <p>刑诉法第291条第一款规定了适用缺席审判案件的类型及条件：</p>
    <p>
      对于
      <strong>贪污贿赂犯罪</strong>案件，以及需要及时进行审判，经最高人民检察院核准的
      <strong>严重危害国家安全犯罪、恐怖活动犯罪案件</strong>，犯罪嫌疑人、被告人
      <strong>在境外</strong>，监察机关、公安机关移送起诉，人民检察院认为
      <strong>犯罪事实已经查清，证据确实、充分</strong>，依法应当追究刑事责任的，可以向人民法院提起公诉。
    </p>
    <p>人民法院进行审查后，对于起诉书中有明确的指控犯罪事实，符合缺席审判程序适用条件的，应当决定开庭审判。</p>
    <p>另据2021年3月1日起施行的《最高人民法院关于适用〈中华人民共和国刑事诉讼法〉的解释》，对人民检察院依照刑事诉讼法第291条第一款的规定提起公诉的案件，人民法院审理后应当参照本解释第295条的规定作出判决、裁定。</p>
    <p>
      该解释第295条规定，对第一审公诉案件，人民法院审理后，起诉指控的事实清楚，证据确实、充分，依据法律认定指控被告人的罪名成立的，应当
      <strong>作出有罪判决。</strong>
    </p>
    <p>
      上述解释还提到，适用缺席审判程序审理案件，
      <strong>可以对违法所得及其他涉案财产一并作出处理。</strong>
    </p>
    <p>张建伟在接受《法治日报》采访时指出，在对潜逃贪官逃往国进行引渡申请或谈判时，一些国家往往会考虑其是否已被该逃出国依法定罪。</p>
    <p>
      “
      <strong>一旦确定了罪犯身份，根据公约，一般情况下签约国就有义务支持引渡申请。</strong>因此，在世界范围内通缉腐败犯罪已成为各国面临的共同问题，如何有效打击外逃贪腐人员成为我国设立缺席审判制度重点考虑的问题。”
    </p>
    <p>最后再来解释一个疑问。</p>
    <p>可能有读者问，假如程三昌被抓回来了怎么办？如果判决生效后才被抓回来又怎么处理？</p>
    <p>
      刑诉法的回答是，
      <strong>在审理过程中，被告人自动投案或者被抓获的，人民法院应当重新审理。罪犯在判决、裁定发生法律效力后到案的，人民法院应当将罪犯交付执行刑罚。</strong>
    </p>
    <p>交付执行刑罚前，人民法院应当告知罪犯有权对判决、裁定提出异议。罪犯对判决、裁定提出异议的，人民法院应当重新审理。</p>
